import './styles/profile.css';
import profile from '../img/profile.svg';

export default function Profile(){
	return (
		<div className="profile">
			<h2> </h2>
			<img src={profile} className="photo"/>
			<div className="info">
				<p>: </p>
				<p>: </p>
				<p> : 2021-2025</p>
			</div>
		</div>
	);
}
