import './styles/events.css';
import img_1 from '../img/1.svg';
import img_2 from '../img/2.svg';
import img_3 from '../img/3.svg';
import img_4 from '../img/4.svg';

export default function Events(){
	return (
		<div className="events">

			<h2></h2>

			<div className="carousel">
				<div className="line">
					<div className="carousel-item" style={{backgroundImage: `url(${img_1})`}}></div>
					<div className="carousel-item" style={{backgroundImage: `url(${img_2})`}}></div>
					<div className="carousel-item" style={{backgroundImage: `url(${img_3})`}}></div>
					<div className="carousel-item" style={{backgroundImage: `url(${img_4})`}}></div>
				</div>
			</div>

			<h2> </h2>

			<div>
				<div className="line-item">1</div>
				<div className="line-item">2</div>
			</div>

		</div>
	);
}