import {useState} from 'react';
import {Routes, Route, Link} from 'react-router-dom';

import './App.css';
import icon_1 from './img/icon-1.svg';
import icon_2 from './img/icon-2.svg';
import icon_3 from './img/icon-3.svg';
import icon_4 from './img/icon-4.svg';
import menu from './img/menu.svg';

import News from './pages/news';
import Events from './pages/events';
import Time_table from './pages/time_table';
import Profile from './pages/profile'; 

function App() {

    const [sidebarState, sidebarStateChange] = useState(true);

    function sidebarFunc() {
        let sidebar = document.querySelector('.sidebar');
        if(sidebarState){
            sidebar.style.left = '0px';
        }
        else{
            sidebar.style.left = '-200px';
        }
        sidebarStateChange(!sidebarState);
    }

    return (
        <div className="App">

            <div className="sidebar">
                <div className="link">
                    <a href="#"></a>
                    <a href="#"></a>
                    <a href="#"></a>
                    <a href="#"></a>
                    <a href="#"></a>
                </div>
            </div>
            
            <div className="header">
                <span className="App-title"> , !</span>
                <button 
                    className="sidebar-btn" 
                    style={{ backgroundImage: `url(${menu})` }}
                    onClick={sidebarFunc}
                >        
                </button>
            </div>

            <Routes>
                <Route path="/" element={<News/>} />
                <Route path="/events" element={<Events/>} />
                <Route path="/time_table" element={<Time_table/>} />
                <Route path="/profile" element={<Profile/>} />
            </Routes>

            <div className="navbar">
                <Link className="page-link" to="/" style={{ backgroundImage: `url(${icon_1})` }}/>
                <Link className="page-link" to="/events" style={{ backgroundImage: `url(${icon_2})` }}/>
                <Link className="page-link" to="/time_table" style={{ backgroundImage: `url(${icon_3})` }}/>
                <Link className="page-link" to="/profile" style={{ backgroundImage: `url(${icon_4})` }}/>
            </div>

        </div>
    );

}

export default App;
